.btn{border-radius:10px;padding-top:.375rem;padding-bottom:.375rem;text-transform:uppercase}.btn:hover{background-color:white;color:#dd405d}.start .scissors,.start .paper,.start .rock{border:20px solid transparent;padding:1.875rem;border-radius:50%;height:9.375rem}@media (min-width: 300px){.playing .scissors,.playing .paper,.playing .rock{border:20px solid transparent;padding:1.875rem;border-radius:50%;height:9.375rem}}@media (min-width: 700px){.playing .scissors,.playing .paper,.playing .rock{border:30px solid transparent;padding:3.125rem;border-radius:50%;height:16.25rem}}.scissors{background:linear-gradient(white, white) padding-box,linear-gradient(#ec9e0e,#eca922) border-box}.paper{background:linear-gradient(white, white) padding-box,linear-gradient(#4865f4,#5671f5) border-box}.rock{background:linear-gradient(white, white) padding-box,linear-gradient(#dc2e4e,#dd405d) border-box}.selecting{border-radius:50%;background:rgba(34,32,32,0.3)}@media (min-width: 300px){.selecting{width:6.25rem;height:6.25rem}}@media (min-width: 700px){.selecting{width:15.625rem;height:15.625rem}}*{font-family:"Barlow Semi Condensed", sans-serif}body{background-image:repeating-radial-gradient(#1f3756,#141539)}

